* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background: $bg-color;
  font-family: Poppins, sans-serif;
  font-size: $main-font-size;
  color: $font-color;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
